import { useTranslation } from "react-i18next";
import { useStudentProfileAttendanceStatsLectureWise } from "../../../Hooks/ApiHooks/StudentManager/student-manager-api";
import style from "../StudentProfile.module.css";
const StudentProfileAttendanceStatsLecture = ({ sid }) => {
  const { t } = useTranslation();

  const { studentProfileAttendanceStatsLectureWise } =
    useStudentProfileAttendanceStatsLectureWise({
      data: {
        sid: sid,
      },
      skip: !sid,
    });
  return (
    <>
      <div className={style.spasd_card}>
        <div
          className={style.spasdc_inner}
          style={{
            "--bg-attendance-stats": "rgba(37, 172, 74, 0.12)",
            marginBottom: "0.2rem",
          }}
        >
          <p>{t("present")}</p>
          <p>
            {studentProfileAttendanceStatsLectureWise?.presentLecture ?? 0}{" "}
            lectures
          </p>
          <p>
            {studentProfileAttendanceStatsLectureWise?.presentPercentage ?? 0} %
          </p>
        </div>
        <div
          className={style.spasdc_inner}
          style={{
            "--bg-attendance-stats": "rgba(238, 56, 51, 0.12)",
          }}
        >
          <p>{t("absent")}</p>
          <p>
            {studentProfileAttendanceStatsLectureWise?.absentLecture ?? 0}{" "}
            lectures
          </p>
          <p>
            {studentProfileAttendanceStatsLectureWise?.absentPercentage ?? 0} %
          </p>
        </div>
      </div>
    </>
  );
};

export default StudentProfileAttendanceStatsLecture;
