import { useTranslation } from "react-i18next";
import { useStudentProfileAttendanceStatsDayWise } from "../../../Hooks/ApiHooks/StudentManager/student-manager-api";
import style from "../StudentProfile.module.css";
import dayjs from "dayjs";
const StudentProfileAttendanceStatsDay = ({ sid }) => {
  const { t } = useTranslation();

  const { studentProfileAttendanceStatsDayWise } =
    useStudentProfileAttendanceStatsDayWise({
      data: {
        sid: sid,
        date: dayjs().format("DD/MM/YYYY"),
      },
      skip: !sid,
    });
  return (
    <>
      <div className={style.spasd_card}>
        <div
          className={style.spasdc_inner}
          style={{
            "--bg-attendance-stats": "rgba(37, 172, 74, 0.12)",
            marginBottom: "0.2rem",
          }}
        >
          <p>{t("present_today")}</p>

          <p>
            {studentProfileAttendanceStatsDayWise?.attendance_status ===
            "Present"
              ? 100
              : 0}{" "}
            %
          </p>
        </div>
        <div
          className={style.spasdc_inner}
          style={{
            "--bg-attendance-stats": "rgba(238, 56, 51, 0.12)",
          }}
        >
          <p>{t("absent_today")}</p>

          <p>
            {studentProfileAttendanceStatsDayWise?.attendance_status ===
            "Absent"
              ? 100
              : 0}{" "}
            %
          </p>
        </div>
      </div>
    </>
  );
};

export default StudentProfileAttendanceStatsDay;
