import { useState } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import StudentProfileAttendanceStatsDay from "./StudentProfileAttendanceStatsDay";
import StudentProfileAttendanceStatsLecture from "./StudentProfileAttendanceStatsLecture";
import style from "../StudentProfile.module.css";
import { useTranslation } from "react-i18next";

const StudentProfileAttendanceStats = ({ sid }) => {
  const { t } = useTranslation();
  const [step, setStep] = useState(1);

  return (
    <>
      <div className={style.spas_heading}>
        <h6>{t("attendance")}</h6>
      </div>

      <div className={style.spas_container}>
        <div className={style.spasc_inner} onClick={() => setStep(1)}>
          <p>{t("day_wise")}</p>
          <img
            src={
              step === 1
                ? `${assestsUtilityUrl}/radio-blue.svg`
                : `${assestsUtilityUrl}/radio-grey.svg`
            }
            alt="radio icon of attendance"
          />
        </div>
        <div className={style.spasc_inner} onClick={() => setStep(2)}>
          <p>{t("Lecture_wise")}</p>
          <img
            src={
              step === 2
                ? `${assestsUtilityUrl}/radio-blue.svg`
                : `${assestsUtilityUrl}/radio-grey.svg`
            }
            alt="radio icon of attendance"
          />
        </div>
      </div>
      {step === 1 ? (
        <StudentProfileAttendanceStatsDay sid={sid} />
      ) : (
        <StudentProfileAttendanceStatsLecture sid={sid} />
      )}
    </>
  );
};

export default StudentProfileAttendanceStats;
